@mixin base {
    background-image: linear-gradient(#688465, #688465);
    background-image: url(../../../images/conference.png);
    background-repeat: no-repeat;
    background-size: cover;
    min-height: 100vh;
    // display: flex;
    align-items: center;
}

.efficiencies {
    @include base;

    @import 'efficiencies-1';
}