@mixin base {
    padding: 2rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    align-items: center;

    @media screen and (min-width: 768px) {
        padding: 4rem;
    }


}

.comparison-1,
.comparison-3 {
    @include base;

    & h1 {
        font-size: 6rem;
    }

    & p {
        font-size: 2rem;
    }

    & li {
        font-size: 2.4rem;
        font-weight: normal;
        list-style-type: none;
    }

    &__value {
        font-size: 12.9rem;
        font-weight: normal;
    }

    &__content {
        color: #8c8c8c;
        font-size: 2rem;
    }

    @media screen and (min-width: 768px) {

    }
}

.comparison-2 {
        @include base;

    li {
            font-size: 2.4rem;
            font-weight: normal;
            list-style-type: none;
            color: #8c8c8c;
            margin-top: -3rem;
        }
    }

.comparison-3 {
    margin-bottom: 31rem;
}

