@mixin base {
    padding: 2rem;
    min-height: 100vh;

    @media screen and (min-width: 1330px) {
        min-height: 50vh;
    }
}

.projects-a {
    @include base;

    & h1 {
        margin-bottom: 0;
        line-height: 0.8;
        font-weight: 400;
    }
}

.piloting-1 {
    list-style-type: none;

    &__value {
        font-size: 9rem;
        font-weight: 700;
        padding-bottom: 2rem;

        @media screen and (min-width: 768px) {
            font-size: 12rem;
        }
    }

    &__label {
        font-size: 2rem;
        padding-bottom: 2rem;
        @media screen and (min-width: 768px) {
            font-size: 3.5rem;
        }
    }

    &__para {
        font-size: 2.8rem;
        text-align: center;
        font-variant: small-caps;
    }
}